@font-face {
  font-family: myFont;
  src: url(/snake/static/media/Gugi-Regular.deba6882.ttf);
}
body {
  background: url(/snake/static/media/funky-lines.7c2eac87.png);
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-weight: bold;
}
h3,
.score {
  font-family: myFont;
}
code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.container {
  width: 80%;
  max-width: 600px;
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  padding: 0px;
  overflow: hidden;
  background: rgb(245, 245, 245);
  box-shadow: 5px 5px 5px lightgrey;
}

:root {
  --cell-border-color: rgb(37, 37, 37);
}
.grid-cell {
  display: inline-block;
  margin: 0;
  padding: 0;
  height: 15px;
  width: 15px;
  border-left: 0.5px solid rgb(37, 37, 37);
  border-left: 0.5px solid var(--cell-border-color);
  border-bottom: 0.5px solid rgb(37, 37, 37);
  border-bottom: 0.5px solid var(--cell-border-color);
}
/* border for every cell in the first row */
.row-0.grid-cell {
  border-top: 0.5px solid rgb(37, 37, 37);
  border-top: 0.5px solid var(--cell-border-color);
}
/* border on the right cell in every row */
.row.grid-cell:last-child {
  border-right: 0.5px solid rgb(37, 37, 37);
  border-right: 0.5px solid var(--cell-border-color);
}
.food {
  background: #6cca51;
}
.snake {
  background: #fbcb38;
}

.content {
  text-align: center;
  background: #e3e6e7;
}
header {
  background: #858dbb;
  padding: 20px;
  display: flex;
  align-items: center;
  justify-content: space-between;
}
h3 {
  padding: 5px;
  margin: 10px;
  font-size: 140%;
}
h3 .status {
  display: inline-block;
  height: 10px;
  width: 10px;
  margin: 0 5px;
  border-radius: 50%;
  cursor: pointer;
  position: relative;
  right: 5px;
  -webkit-animation: 2s mix infinite;
          animation: 2s mix infinite;
}
.score {
  padding: 10px;
  border: 1px solid transparent;
}
.in-progress {
  background: #fbcb38;
}
.finished {
  background: #d84339;
}
.score.finished {
  border: 1px solid white;
  padding: 10px;
  background: none;
}
.not-started {
  background: #fff;
}
.paused {
  background: #f1cbb5;
}
.grid-row {
  padding: 0;
  margin: 0;
  height: 15px;
}
.board {
  margin: 45px 0;
  display: inline-block;
}
.play {
  position: absolute;
  right: 0;
  bottom: 0;
  font-size: 25px;
  margin: 13px;
  color: rgb(24, 24, 24);
  cursor: pointer;
  transition: 0.7s;
}
.play:hover {
  -webkit-transform: scale(1.5) rotate(360deg);
          transform: scale(1.5) rotate(360deg);
}
@-webkit-keyframes mix {
  0% {
    -webkit-transform: scale(0.5);
            transform: scale(0.5);
  }
  50% {
    -webkit-transform: scale(1.2);
            transform: scale(1.2);
  }
  100% {
    -webkit-transform: scale(0.5);
            transform: scale(0.5);
  }
}
@keyframes mix {
  0% {
    -webkit-transform: scale(0.5);
            transform: scale(0.5);
  }
  50% {
    -webkit-transform: scale(1.2);
            transform: scale(1.2);
  }
  100% {
    -webkit-transform: scale(0.5);
            transform: scale(0.5);
  }
}

